.pokemonInfoItem {
  display: grid;
  height: 576px;
  background-color: #ececec;
  h2 {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 36px;
    margin: 0;
  }
  text-transform: capitalize;
  .ant-avatar {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 300px;
    width: 300px;
  }
  svg {
    height: 250px;
    width: 250px;
  }
}

.Loader {
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.pokemon {
  background-color: #ffffff;
  height: 400px;
  width: 576px;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-direction: column;
  img {
    width: 384px;
    height: 384px;
  }
  div {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    font-size: 16px;
    width: 500px;
  }
}
.row {
  display: grid;
  align-items: center;
  justify-content: center;
  grid-template-columns: 576px 576px;
  grid-gap: 0px 20px;
}
.pokemon.typesDiv {
  justify-self: center;
  align-self: center;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100px;
}
